
@media screen and (max-width:600px) {
    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_cards {
        grid-template-columns:  1fr 1fr;
        gap :1rem;
    }
    .about_content{
        text-align: center;
    }
    .about_content p {
        margin:1.5rem 0;
    }
}
.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap:15%
}
.about_me{
    width: 110%;
    aspect-ratio: 1/1;
    border-radius: 10%;
    background: linear-gradient(45deg,transparent,rgba(42, 165, 196, 0.8),transparent);
    display: grid;
    place-items:center ;
}
.about_me-image{
    border-radius: 10%;
    overflow: hidden;
    transform: rotate(10deg);
    transition: ease-in-out 0.1s;
}
.about_me-image:hover{
   transform: rotate(0);
   transition: ease-in-out 0.2s;
}
.about_cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:1.5rem;
}
.about_card{
    background:fixed;
    background-color: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: ease-in-out 0.2s;
}
.about_card:hover{
   background:transparent;
   border-color: rgba(255,255,255,0.4);
   transition: ease-in-out 0.2s;
}
.about_icon{
    color:var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about_cards h5{
    font-size: 0.5rem;
}
.about_card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.btn {
    align-items: center;
}
.about_content p{
    margin:2rem 0 2.6rem;
    color:var(--color-light)
}


/*  */
@media screen and (max-width:1024px) {
    .about_container {
        grid-template-columns: 1fr;
        gap:0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;

    }
    .about_content p{
        margin: 1rem 0 1.5rem;
    }
}
/* Media Query Small */
