header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    text-align: center;

}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
/* /CTA=================== */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;

}
/* /social_icon=================== */
.header_social{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left: 0;
    bottom: 2rem;
}
.header_social:after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
/* /images=================== */
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 20rem;
    height: 30rem;
    position: absolute;
    left:calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    
}
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);;
    font-weight: 300;
    font-size: 0.9rem;
}
/* Media Query Max*/
@media screen and (max-width:1024px) {
    header{
        height: 100vh;
        text-align: center;
       
    }
}
/* Media Query Small */
@media screen and (max-width:600px) {
    header{
        height: 100vh;
    }
    .header_social,
    .scroll_down {
        display: none;
    }
}